<template>
  <PageWrapper dense contentFullHeight fixedHeight contentClass="flex" style="position: relative">
    <CommonTree
      title="电站组织"
      @select="handleSelect"
      :value="treeData"
      class="left-tree-layout"
      :toolbar="true"
      :canEdit="false"
      :canAdd="false"
      :canDelete="false"
      :isShowOperationBtns="false"
      :fieldNames="{ key: 'id', title: 'name' }"
    />
    <div class="right-table-layout">
      <BasicTable
        @register="registerTable"
        class="fix-basic-table"
        :searchInfo="searchInfo"
        @resize-column="(w, col) => (col.width = w)"
        @row-db-click="(record) => handleReadOnlyView(record)"
        @selection-change="handleSelectChange"
      >
        <template #toolbar>
          <a-button type="primary" :preIcon="IconEnum.ADD" @click="handleCreate">
            {{ t('common.action.create') }}
          </a-button>
          <a-button
            class="red-btn"
            :disabled="selectedRowId == ''"
            :preIcon="IconEnum.DELETE"
            @click="handleDelete"
            >{{ t('common.delText') }}</a-button
          >
          <a-button class="yellow-btn" :preIcon="IconEnum.EXPORT" @click="handleExport">
            {{ t('common.action.export') }}
          </a-button>
        </template>
      </BasicTable>
    </div>
  </PageWrapper>
</template>
<script lang="ts" setup>
  import { ref, onMounted, reactive } from 'vue';
  import { TreeItem } from '/@/components/Tree';
  import CommonTree from '@/components/Framework/Tree/CommonTree.vue';
  import { PageWrapper } from '/@/components/Page';
  import { BasicTable, useTable, SorterResult } from '@/components/Table';
  import { useI18n } from '@/hooks/web/useI18n';
  import { IconEnum } from '@/enums/appEnum';
  import { useMessage } from '@/hooks/web/useMessage';
  import { message } from 'ant-design-vue';
  import { assign } from 'lodash-es';
  import { exportExcelFile } from '@/utils/file/download';
  import {
    columns,
    searchFormSchema,
    getStationPeriodTreeList,
    getInfoarchiveList,
    exprotInfoarchiveList,
    getInfoarchive,
  } from './data';
  import { addTabPage } from '@/utils/route';
  import { MsgManager } from '@/message/MsgManager';
  import * as ProcessInstanceApi from '@/api/bpm/processInstance';
  import * as CommonUtil from '/@/utils/common';

  const { t } = useI18n();
  const searchInfo = reactive<any>({});
  const treeData = ref<TreeItem[]>([]);
  const selectedNode = reactive<any>({});
  const { createConfirm, createMessage } = useMessage();
  let selectedRowId = ref('');
  //代码启动执行
  onMounted(() => {
    getStationTreeData();
    MsgManager.getInstance().listen('infoarchive', reload);
  });

  //查询电站组织树
  async function getStationTreeData() {
    treeData.value = (await getStationPeriodTreeList({})) as unknown as TreeItem[];
    reload();
  }

  //电站组织树选中事件
  function handleSelect(node) {
    searchInfo.stationOrganId = null;
    searchInfo.stationId = null;
    searchInfo.periodId = null;
    if (node) {
      searchInfo.stationOrganId = node.organId;
      searchInfo.stationId = node.powerStationId;
      searchInfo.periodId = node.powerStationPeriodId;
      selectedNode.stationOrganId = node.organId;
      selectedNode.stationOrganName = node.organName;
      selectedNode.stationId = node.powerStationId;
      selectedNode.stationName = node.powerStationName;
      selectedNode.periodId = node.powerStationPeriodId;
      selectedNode.periodName = node.powerStationPeriodName;
      selectedNode.nodeKindId = node.nodeKindId;
    }
    reload();
  }

  const getInfoarchiveListParam = async () => {
    const paginationRef = getPaginationRef() as any;
    const searchPageInfo = reactive<any>({});
    assign(searchPageInfo, searchInfo);
    searchPageInfo.pageNo = paginationRef.current;
    searchPageInfo.pageSize = paginationRef.pageSize;
    if (!searchPageInfo.order) {
      searchPageInfo.field = 'fillinDate';
      searchPageInfo.order = 'desc';
    }
    let dataObj = await getInfoarchiveList(searchPageInfo);
    return dataObj;
  };

  const [registerTable, { reload, getSelectRows, getPaginationRef }] = useTable({
    title: '信息档案列表',
    api: getInfoarchiveListParam,
    columns: columns,
    formConfig: searchFormSchema,
    useSearchForm: true,
    fetchSetting: {
      listField: 'list',
      pageField: 'pageNo',
      sizeField: 'pageSize',
      totalField: 'total',
    },
    handleSearchInfoFn(info) {
      assign(searchInfo, info);
      return info;
    },
    sortFn: (sortInfo: SorterResult) => {
      if (sortInfo.order) {
        searchInfo.sortField = sortInfo.field;
        searchInfo.sortOrder = sortInfo.order.replace('end', '');
      } else {
        searchInfo.sortField = '';
        searchInfo.sortOrder = '';
      }
    },
    bordered: true,
    pagination: { pageSize: 20 },
    showIndexColumn: true,
    showTableSetting: true,
    tableSetting: { fullScreen: true },
    rowSelection: { type: 'radio', fixed: true, onSelect: onSelect },
  });

  //新增
  function handleCreate() {
    if (!selectedNode.periodId) {
      message.error('请先选择电站分期。');
      return;
    }
    addTabPage(`/po/integrated/infoarchive/create`, '添加信息档案', {
      stationOrganId: selectedNode.stationOrganId,
      stationOrganName: selectedNode.stationOrganName,
      stationId: selectedNode.stationId,
      stationName: selectedNode.stationName,
      periodId: selectedNode.periodId,
      periodName: selectedNode.periodName,
      id: '',
    });
  }

  function handleReadOnlyView(record: any) {
    CommonUtil.toFlowPage(record.id);
  }

  function handleSelectChange({ keys, rows }) {
    if (rows && rows.length > 0) {
      const record = rows[0];
      selectedRowId.value = record.id;
    } else {
      selectedRowId.value = '';
    }
  }

  function onSelect(record, selected) {
    if (selected) {
      selectedRowId.value = record.id;
    } else {
      selectedRowId.value = '';
    }
  }

  //获取表格选择数据
  function checkSelectData() {
    const selectRows = getSelectRows();
    if (selectRows.length == 0) {
      message.error('请选择一行数据');
      return false;
    }
    return selectRows[0] as any;
  }

  //删除
  async function handleDelete() {
    const row = checkSelectData();
    if (!row.id) {
      return;
    }
    const res = await getInfoarchive({ id: row.id });

    createConfirm({
      title: '提示',
      iconType: 'warning',
      content: '确定要删除选中记录？',
      async onOk() {
        // 发起取消
        if (res.result.status != 0) {
          message.error('非草稿状态不能删除。');
          return;
        }
        await ProcessInstanceApi.deleteProcessInstance(res.result.processInstanceId);
        message.success('已删除。');
        reload();
      },
    });
  }

  //导出
  function handleExport() {
    createConfirm({
      title: t('common.exportTitle'),
      iconType: 'warning',
      content: t('common.message.exportMessage'),
      async onOk() {
        await exprotInfoarchiveList(searchInfo).then((res) => {
          exportExcelFile(res?.data, '信息档案.xls');
        });
        createMessage.success(t('common.exportSuccessText'));
      },
    });
  }
</script>
<style lang="less" scoped>
  .left-tree-layout {
    flex-shrink: 0;
    width: 298px;
    border-radius: 2px;
  }

  .right-table-layout {
    width: calc(100% - 308px);
  }

  .fix-basic-table {
    :deep(&.vben-basic-table .ant-table-wrapper) {
      padding: 10px 16px 12px;
    }

    :deep(.table-settings) {
      margin-right: 0;
    }

    :deep(.table-settings .anticon-setting) {
      margin-right: 0;
    }
  }
</style>
